import React, {Component} from 'react';
import EntryList from '../components/EntryList';
import SearchBox from '../components/SearchBox';
import Pokedex from 'pokedex-promise-v2';
import './App.css';

const P = new Pokedex();
const fetchPokemon = async function(numberOfPokemon) {
	try {
		const pokemonList = [];
		let j = 0;
		while(j < numberOfPokemon) {
			const testPokemon = await P.getPokemonByName(j+1);
			pokemonList.push(testPokemon);
			j++;		
		}
		console.log(pokemonList);
		return pokemonList;
	} catch (err) {
		console.log('shit', err);
	}
}

class App extends Component {
	constructor() {
		super()
		this.state = {
			pokemon: [],
			searchfield: ''
		}
	}

	componentDidMount() {
		fetchPokemon(10).then(pokeList => this.setState({pokemon: pokeList}));
	}

	onSearchChange = (event) => {
		this.setState({searchfield: event.target.value});
	}

	render() {
		const {pokemon, searchfield} = this.state;
		const filteredPokemon = pokemon.filter(dexEntry => {
			return dexEntry.name.toLowerCase().includes(searchfield.toLowerCase());
		})
		return !pokemon.length ?
			<h1 className='tc f1'>Loading</h1>:
			(
				<div className='tc'>
					<h1 className='f-5'>Pokedex</h1>
					<SearchBox searchChange={this.onSearchChange}/>
					<EntryList pokemon={filteredPokemon} />
				</div>
			)
	}
}

export default App;